.App {
  width: 100%;
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  align-items: center;
}

.textbox {
  margin-top: 50px;;
  width: 50%;
}


.name {
  text-decoration: underline;
  text-decoration-color: red;
}

a {
  text-decoration: none;
  color: #09d3ac;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  /* flex-direction: column; */
  justify-content: space-between;
  width: 70%;
  animation: animation 3s;
  display: none;
  /* margin-bottom: 20px; */
  font-size: calc(8px + 2vmin);
}

.project {
  min-height: auto;
  display: inherit;
  /* justify-content: space-between; */
  flex-direction: column;
  /* flex-wrap: wrap; */
  width: 45%;
  /* background-color: pink; */
  border: white solid 1px;
  margin-top: 2rem;
  padding: 1rem;
}

.footer {
  font-size: calc(1px + 2vmin);
  display: none;
  margin: none;
  padding: 30px;;
}

h3 {
  display: inline-block;
  margin: 0px;
  text-align: left;
  padding: 0px;
  text-decoration: underline;
}

p {
  padding: 0px;
  margin-top: 5px;
  margin-bottom: 0px;
  text-align: left;
}

@keyframes animation {
  from {opacity: 0;}
  to {opacity: 100;}
}

@media only screen and (max-width: 1000px) {
  .textbox {
    width: 100%;
  }

  .projects {
    width: 90%;
    flex-direction: column;
  }

  .project {
    width: 90%;
    /* flex-direction: column; */
  }

  .footer {
    font-size: calc(3px + 2vmin);
    padding: 30px;;
  }
}